import React, { Component } from "react";
import styled from "styled-components";
import Header from "../Component/Header";
import Contents from "../Component/Contents";
import Buttons from "../Component/Buttons";
import Footer from "../Component/Footer";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faArrowLeft,
  faHome,
  faCopy,
  faSave
} from "@fortawesome/free-solid-svg-icons";
import { connect } from "react-redux";
import * as actions from "../Actions";
import { saveAs } from "file-saver";
import FinishPic from "../Img/Finish.png";

const Container = styled.div`
  display: flex;
  width: 100%;
  height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;
class Offer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.save = this.save.bind(this);
  }
  save() {
    let blob = new Blob([this.props.text.sum], {
      type: "text/plain;charset=utf-8"
    });
    const date = new Date();
    const seconds = date.getSeconds();
    const minute = date.getMinutes();
    const hour = date.getHours();
    const time = hour + ":" + minute + ":" + seconds;
    saveAs(blob, "OREO_" + time + ".txt");
  }

  componentDidMount() {}
  render() {
    return (
      <div className="fadeIn">
        <Container>
          <Header title={FinishPic} desc="" stage="1"></Header>
          <Contents
            stage="sum"
            placeholder="내용을 저장할 수 있어요!"
          ></Contents>
          <Buttons
            array={[
              [<FontAwesomeIcon icon={faSave} />, "/finish", this.save],
              [<FontAwesomeIcon icon={faHome} />, "/", this.props.clear]
            ]}
          ></Buttons>
        </Container>
        {/* <Footer stage="end"></Footer> */}
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    text: state.Reducer.text
  };
};

const mapDispatchToProps = dispatch => {
  return {
    upload: (title, content) => {
      dispatch(actions.write(title, content));
    },
    clear: () => {
      dispatch(actions.clear());
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Offer);
