import React, { Component } from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import * as actions from "../Actions";
import { Beforeunload } from "react-beforeunload";

// import write from "../Reducers";

const Container = styled.div`
  width: 100%;
  display: flex;
  margin-bottom: 30px;
  justify-content: center;
`;

const Box = styled.div`
  width: 60%;
  min-width: 400px;
  display: flex;
  justify-content: center;
`;

const Form = styled.form`
  padding-bottom: 5px;
  display: flex;
  justify-content: center;
  width: 400px;
`;

const Input = styled.div`
  background-color: transparent;
  /* padding-bottom: 10px; */
  /* height: ${props => (props.stage === "end" ? "120px" : "70px")}; */
  max-width: 300px;
  text-align: center;
  line-height: 1.3;
  caret-color: rgba(24, 12, 12, 1);
  color: rgba(24, 12, 12, 1);
  /* border-bottom: solid 3px rgba(24, 12, 12, 0.3); */
  font-size: 24px;
  font-weight: 800;
  /* border-bottom: solid 2px black; */
  &::placeholder {
    color: rgba(24, 12, 12, 0.3);
  }
  &:focus,
  &:hover {
    /* border-bottom: solid 3px rgba(24, 12, 12, 1); */
  }
`;

class Contents extends Component {
  constructor(props) {
    super(props);
    this.state = {};
    this.inputFormHandler = this.inputFormHandler.bind(this);
    this.sumContent = this.sumContent.bind(this);
  }

  componentDidMount() {
    if (this.props.stage === "sum") {
      const sum = this.sumContent();
      document.getElementsByName("sum")[0].innerHTML = sum;
    } else {
      const content = this.props.text[this.props.stage];
      document.getElementsByName(this.props.stage)[0].innerHTML = content;
    }
  }
  inputFormHandler(e) {
    const title = this.props.stage;
    const content = document.getElementsByName(this.props.stage)[0].innerHTML;
    // console.log(title, content);
    this.props.upload(title, content);
  }

  sumContent() {
    const { opinion, reason, example, offer } = this.props.text;
    if (opinion === "" && reason === "" && example === "" && offer === "") {
      return "";
    }
    const sum = opinion + " " + reason + " " + example + " " + offer;

    return sum;
  }

  render() {
    const { stage, placeholder } = this.props;
    return (
      <Beforeunload onBeforeunload={() => "You'll lose your data!"}>
        <Container>
          <Box>
            <Form>
              <Input
                name={stage}
                contentEditable={true}
                onInput={this.inputFormHandler}
                spellCheck="false"
                // value={
                //   stage === "sum" ? this.sumContent() : this.props.text[stage]
                // }
                placeholder={placeholder}
              ></Input>
            </Form>
          </Box>
        </Container>
      </Beforeunload>
    );
  }
}

const mapStateToProps = state => {
  return {
    text: state.Reducer.text
  };
};

const mapDispatchToProps = dispatch => {
  return {
    upload: (title, content) => {
      dispatch(actions.write(title, content));
    }
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Contents);
